






















import Vue from "vue";
import Page from "@/mixins/Page";
import Banner from "@/components/Banner.vue";
import ProjectTable from "@/components/ProjectTable.vue";
import ProjectForm from "@/components/ProjectForm.vue";

export default Vue.extend({
  mixins: [Page],
  components: {
    Banner,
    ProjectTable,
    ProjectForm,
  },
  data() {
    return {
      isOpen: false,
      projects: [],
      project: {
        name: "",
        summary: "",
        client: "",
      },
    };
  },
  methods: {
    LoadProjects: function() {
      const that = this;
      //@ts-ignore
      window.backend.Storage.ListProjects()
        .then((res: any) => {
          that.projects = res;
          return res;
        })
        .catch((err: Error) => {
          console.error(err);
          return [{}];
        });
    },
  },
  mounted() {
    this.LoadProjects();
  },
  computed: {
    classObject: function() {
      return {
        panel: true,
      };
    },
  },
});
